.container-sidebar-content-details {
    display: flex;
    flex-direction: row;
}

.pokemon-sidebar {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 30vh;
    height: calc(100vh - 3rem);
    margin: 0rem;
    padding: 0rem;
    overflow: hidden;
}

.pokemon-sidebar-link {
    width: 100%;
    height: calc(100% / 18);
}

.pokemon-sidebar-contenedor-categoria {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    width: 100%;
    height: 100%;
    text-shadow: 1px 1px 4px rgba(255, 255, 255, 0.5) !important;
    color: black;
    font-weight: 600;
    transition: .3s ease-in-out;
}

.pokemon-sidebar-img-ico-category {
    width: auto;
    height: calc(100% / 1.2);
    margin-left: .5rem;
}

.pokemon-sidebar-name-category {
    font-size: 1rem;
    font-weight: 600;
    margin-left: .5rem;
}